import uCharts from '../../components/u-charts/u-charts.js'
var _self;
var canvaLineA = null;
export default {
	data() {
		return {
			time_range: ['08:25', '09:45', '10:22', '12:12', '13:25', '16:25', '18:25'],
			dateList: [{
					date: '2020年2月19日',
					time: ['09:55:16', '10:25:26', '09:55:16']
				},
				{
					date: '2020年2月20日',
					time: ['09:55:16', '10:25:26', '09:55:16', '09:55:16']
				},
			],
			bpm: '86BPM',

			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
		};
	},
	onLoad() {
		_self = this;
		_self.cWidth = uni.upx2px(750);
		console.log(_self.cWidth)
		_self.cHeight = uni.upx2px(500);
		_self.getServerData();
	},
	methods: {
		getServerData() {
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data: {},
				success: function(res) {
					console.log(res.data.data)
					let LineA = {
						categories: [],
						series: []
					};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					LineA.categories = res.data.data.LineA.categories;
					// LineA.series = res.data.data.LineA.series;
					LineA.series = res.data.data.LineA.series;
					LineA.series = [LineA.series.pop()];
					_self.showLineA("myChart", LineA);
				},
				fail: () => {
					_self.tips = "网络错误，小程序端请检查合法域名";
				},
			});
		},
		showLineA(canvasId, chartData) {
			canvaLineA = new uCharts({
				$this: _self,
				canvasId: canvasId,
				colors: ['#FFFFFF'],
				type: 'line',
				fontSize: 12,
				dataLabel: false,
				dataPointShape: true,
				background: '#24C789',
				pixelRatio: _self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					gridColor: '#FFF',
					gridType: 'dash',
					disableGrid: true,
					axisLine: false,
					fontColor: '#FFF',
				},
				yAxis: {
					data: [{
						position: 'right',
						fontColor: '#FFF',
						axisLineColor: '#24C789',
						min: 0,
						max: 300,
						format: (val) => {
							return val.toFixed(0)
						}
					}],
					gridType: 'dash',
					gridColor: '#FFF',
					splitNumber: 6,
					dashLength: 2,
				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					line: {
						type: 'straight'
					}
				}
			});

		},
		touchLineA(e) {
			canvaLineA.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		}
	},
}
